<template>
  <div class="about" ref="about">
    <boxmy>
      <div class="content" slot="content">
        <titlemy :title="title" class="titlemy"></titlemy>
        <p class="info" v-for="item of infos" :key="item">{{item}}</p>
      </div>
    </boxmy>
  </div>
</template>
<script>
import { mapMutations } from 'vuex';
import boxmy from '@/components/boxmy';
import titlemy from '@/components/titlemy';

export default {
  name: 'about',
  components: {
    boxmy,
    titlemy,
  },
  data() {
    return {      
      title: {
        en: 'About Us',
        name: '',
        color: '#fff',
      },
      infos: [
        '"CEO',

        'RPO',

        '2014"',
      ],
    };
  },
  props:{
      scrollTopmy:{
          type: Number,
          default: 0
      }
  },
  watch:{
      scrollTopmy(){
          this.handleValue();
      }
  },
  methods: {
    ...mapMutations(['m_aboutHeight','m_aboutOffsetTop']),
    handleValue(){
         setTimeout(()=>{
          this.m_aboutHeight({ aboutHeight: this.$refs.about.clientHeight });
          this.m_aboutOffsetTop({aboutOffsetTop: this.$refs.about.offsetTop});
      },1)
    }
  },
  mounted() {
     this.handleValue();
    
  },
};
</script>
<style lang="scss" scoped>
.about {
  width: 100%;
  height: px2rem(480);
  background: url("../assets/images/about.png");
  background-size: cover;
  margin-top: px2rem(30);
  .content {

    .info {
      font-size: px2rem(16);
      font-weight: normal;
      font-stretch: normal;
      line-height: px2rem(28);
      letter-spacing: 0px;
      color: #ffffff;
      text-align: center;
      margin-bottom: px2rem(45);
margin-top: px2rem(-20);

    }
  }
}
</style>




