<template>
  <div class="vocational" ref="vocational">
    <boxmy>
      <div class="content" slot="content">
        <titlemy :title="title"/>
        <div class="tab">
          <div class="tb">
            <div
              :class="['tabBox',item.hover?'hover':'']"
              v-for="(item,index) of tabs"
              :key="index"
              @mousemove="changeHover(index)"
            >
              <i :class="['icon',item.icon]"></i>
              <h3 class="tabName">{{item.name}}</h3>
              <i class="tabLine icon-wechat"></i>
            </div>
          </div>
        </div>
      </div>
    </boxmy>
    <div class="tc">
      <boxmy>
        <div class="content" slot="content">
          <div class="tabContent" v-for="item of tabContents" :key="item.title" v-show="item.show">
            <h3 class="tcTitle">{{item.title}}</h3>
            <p class="tcInfo" v-for="i of item.tcInfos" :key="i">
              <span class="iconfont icon-lingxingkuai"></span>
              {{i}}
            </p>
            <div class="peixun" v-if="item.title===''">
              <h3 class="tcTitle">{{item.pxTitle}}</h3>
              <img :src="item.teacherImage" alt class="teacher">
              <div class="right">
                <p class="teacherInfo" v-for="t of item.teacherInfos" :key="t">{{t}}</p>
              </div>
            </div>
            <div class="course" v-if="item.title===''">
              <h3 class="ccTitle">{{item.courseTitle}}</h3>
              <div class="ct_cont" v-for="ct of item.cts" :key="ct.tit">
                <h4 class="ct_tit">
                  <span class="iconfont icon-lingxingkuai"></span>
                  {{ct.tit}}
                </h4>
                <p class="ct_info">{{ct.info}}</p>
              </div>
            </div>
          </div>
        </div>
      </boxmy>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState } from "vuex";
import boxmy from "@/components/boxmy";
import titlemy from "@/components/titlemy";

export default {
  name: "vocational",
  data() {
    return {
      title: {
        en: "Business Introduced",
        name: "",
        color: "#333333"
      },
      tabs: [
        {
          icon: "iconfont icon-lietouyewu",
          name: "",
          hover: true
        },
        {
          icon: "iconfont icon-RPOyewu",
          name: "RPO",
          hover: false
        },
        {
          icon: "iconfont icon-laowupaiqian",
          name: "",
          hover: false
        },
        {
          icon: "iconfont icon-peixunyewu",
          name: "",
          hover: false
        },
        {
          icon: "iconfont icon-renliziyuanchengbenyouhua",
          name: "",
          hover: false
        },
        {
          icon: "iconfont icon-canjirenjiuyebaozhangjin",
          name: "",
          hover: false
        }
      ],
      tabContents: [
        {
          title: "",
          tcInfos: [
            "",
            ""
          ],
          show: true
        },
        {
          title: "RPO",
          tcInfos: [
            "",
            "VIP",
            ",",
            "",
            ""
          ],
          show: false
        },
        {
          title: "",
          tcInfos: [
            "",
            "",
            ",,"
          ],
          show: false
        },
        {
          title: "",
          //   tcInfos: [
          //     "",
          //     "",
          //     ",,"
          //   ],
          tcInfos: [
            "CEOLeader"
          ],
          pxTitle: "",
          teacherImage: require("@/assets/images/teacher.png"),
          teacherInfos: [
            "1010KAPS",
            "2010RobertWalters102200",
            "2012LloydMorgan120157000",
            "2015 80%50%30%100%"
          ],
          courseTitle: "",
          cts: [
            {
              tit: "",
              info: ""
            },
            {
              tit: "",
              info: "   "
            },
            {
              tit: "",
              info: "L-0L-1L-1.5L-2BD"
            }
          ],

          show: false
        },
        {
          title: "",
          tcInfos: [
            "",
            "+"
          ],
          show: false
        },
        {
          title: "",
          tcInfos: [
            "101000"
          ],
          show: false
        }
      ]
    };
  },
  props:{
      scrollTopmy: {
          type: Number,
          default: 0
      }
  },
  components: {
    boxmy,
    titlemy
  },
  computed: {
    ...mapState(["scrollTop"])
  },
  watch:{
      scrollTopmy(n,o){
          this.handleValue();
      }
  },
  methods: {
    ...mapMutations(["m_vocationalHeight", "m_vocationalOffsetTop"]),
    changeHover(i) {
      console.log("i is", i);
      this.tabContents.forEach((item, index) => {
        if (index === i) {
          item.show = true;          
        } else {
          item.show = false;
        }
      });
      this.tabs.forEach((item,index)=>{
          if(index === i){
              item.hover = true;
          }else{
              item.hover = false;
          }
      })
    },
    handleValue(){
         setTimeout(() => {
      this.m_vocationalHeight({
        vocationalHeight: this.$refs.vocational.clientHeight
      });
      this.m_vocationalOffsetTop({
        vocationalOffsetTop: this.$refs.vocational.offsetTop
      });
      console.log(
        this.$refs.vocational.clientHeight,
        "vocational.offsetTop",
        this.$refs.vocational.offsetTop
      );
    }, 1);
    }
  },
  mounted() {
   this.handleValue();
  }
};
</script>
<style lang="scss">
.vocational {
  width: 100%;

  .content {
    .tab {
      .tb {
        .tabBox {
          width: px2rem(164);
          height: px2rem(216);
          padding-top: px2rem(42);
          background-color: #ffffff;
          border-radius: px2rem(4);
          border: solid 1px #02c1ff;
          float: left;
          margin-right: px2rem(42);
          &:nth-last-child(1) {
            margin-right: 0;
          }
          &.hover {
            background-image: linear-gradient(-50deg, #0297ff 0%, #26caff 100%);
            border-radius: 4px;
            .tabName {
              color: #fff;
            }
            .icon {
              border: 1px solid #fff;
            }
          }
          .icon {
            width: px2rem(67);
            height: px2rem(67);
            display: block;
            margin: 0 auto;
            font-size: px2rem(37);
            line-height: px2rem(67);
            text-align: center;
            background-image: linear-gradient(-50deg, #0297ff 0%, #26caff 100%);
            border-radius: 50%;
            color: #fff;
          }
          .tabName {
            text-align: center;
            margin-top: px2rem(33);
            margin-bottom: px2rem(17);
            font-size: px2rem(18);
            line-height: px2rem(24);
            font-weight: normal;
            font-stretch: normal;
            letter-spacing: 0px;
            color: #02c1ff;
            white-space: nowrap;
          }
          .tabLine {
            display: block;
            text-align: center;
          }
        }
      }
    }
   
  }
   .tc {
      margin-top: px2rem(78);
      position: relative;
      //   height: px2rem(500);
      background-color: $modelBgColor;
     
      .tabContent {
        position: relative;
        top: 0px;
        left: 0px;
        padding-top: px2rem(20);
        .iconfont {
          font-size: px2rem(12);
          color: $mainColor;
        }
        .tcTitle {
          font-size: px2rem(20);
          font-weight: normal;
          font-stretch: normal;
          line-height: px2rem(30);
          letter-spacing: 0px;
          color: #333333;
          margin-bottom: px2rem(36);
          text-indent: px2rem(40);
        }
        .tcInfo {
          text-indent: px2rem(30);
          font-size: px2rem(16);
          font-weight: normal;
          font-stretch: normal;
          line-height: px2rem(28);
          letter-spacing: 0px;
          color: #333333;
          margin-bottom: px2rem(37);
        }
        .peixun {
          .teacher {
            width: px2rem(187);
            height: px2rem(239);
          }
          .right {
            float: right;
            width: px2rem(873);
            margin-right: px2rem(100);
            p {
              font-size: px2rem(16);
              font-weight: normal;
              font-stretch: normal;
              line-height: px2rem(30);
              letter-spacing: 0px;
              color: #333333;
            }
          }
        }
        .course {
          text-indent: px2rem(40);
          .ccTitle {
            font-size: px2rem(20);
            font-weight: normal;
            font-stretch: normal;
            line-height: px2rem(30);
            letter-spacing: 0px;
            color: #333333;
            margin: px2rem(36) 0;
            text-indent: px2rem(40);
          }
          .ct_cont {
            margin-bottom: px2rem(30);
            .ct_tit {
              font-size: px2rem(16);
              font-weight: bold;
              font-stretch: normal;
              line-height: px2rem(28);
              letter-spacing: 0px;
              color: #333333;
            }
            .ct_info {
              font-size: px2rem(16);
              font-stretch: normal;
              line-height: px2rem(28);
              letter-spacing: 0px;
              color: #333333;
              text-indent: px2rem(60);
            }
          }
        }
      }
    }
}
</style>



